/* App.css */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.Main {
  background-image: url("./YSS_Background.jpg");
  background-size: cover; /* 이미지를 컨테이너에 맞춰서 확대/축소합니다. */
  background-repeat: no-repeat; /* 이미지 반복 없음 */
  background-position: center center; /* 이미지를 가운데 정렬합니다. */
  background-color: #f2f4fb;

  text-align: center;

  display: block;
  margin-left: auto;
  margin-right: auto;
  min-height: 100vh;
  border: 3px;
  box-sizing: border-box;
  width: min(500px, 100%);
  box-shadow: 10px 20px 40px rgba(0, 0, 0, 0.1),
    -10px 20px 40px rgba(0, 0, 0, 0.1);

  padding-bottom: 10px;
  position: relative; /* 위치를 조정하기 위해 relative로 설정합니다. */
}

/* 새로운 클래스 추가 */
.InputWrapper {
  position: absolute;
  bottom: 5%; /* Input을 화면 아래에서 5%에 위치하도록 설정합니다. */
  left: 50%; /* 화면 가로 중앙에 위치하도록 설정합니다. */
  transform: translateX(
    -50%
  ); /* 가로 중앙 정렬을 위해 왼쪽으로 50% 이동합니다. */
  width: 70%; /* 부모 요소의 너비를 100%로 설정하여 가로 사이즈 조정이 가능하게 합니다. */
}

@media only screen and (max-width: 600px) {
  .Main {
    height: 100%; /* 작은 화면에선 너비를 100%로 변경합니다. */
  }

  .InputWrapper {
    width: 70%; /* 작은 화면에선 입력란을 더 좁게 조정합니다. */
  }
}
